/*general*/

a {
  text-decoration: none !important;

  &:hover {
    text-decoration: none !important;
  }
}

img {
  border: 0px;
  image-rendering: optimizeQuality !important;
}

.clear {
  clear: both;
}

body {
  font-size: 16px;
}

input {
  &::-webkit-input-placeholder, &:-ms-input-placeholder, &::-moz-placeholder {
    font-family: "Rlighti" !important;
  }
}

select {
  font-family: "Rlighti" !important;
}

h1, h2, h4, h5, h6 {
  font-family: "uconheavy";
}

.robot-fuente {
  font-family: "Rcondr";
  color: #D4A26F;
}

#direccionreparto {
  font-family: "Rregular" !important;
}

html {
  background: rgb(0, 0, 0);
  font-family: "United Sans SemiCond";
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

.container {
  background-image: url(../img/bg.jpg);
  background-size: 100%;
  font-family: "United Sans SemiCond";
  padding-left: 0px;
  padding-right: 0px;
  background-repeat: repeat;
}

/*top*/

.header-top {
  padding: 30px;
}

.ingreso-top {
  padding-top: 20px;
  text-align: right;
  color: #979797;

  a {
    color: #FFF;
    font-size: 1.4em;

    &:hover {
      color: #706F6F;
    }
  }
}

.modal-open {
  padding-right: 0px !important;
}

.link-salir {
  margin-left: 30px;
}

.cliente-ingresado-top {
  font-size: 1.3em;
  color: #FFF;
}

.slow .contenedor-menu-web {
  transition: down 0.7s;
  -webkit-transition: down 0.7s;
}

.pie-col-4 {
  padding-top: 2%;
}

/* HOME */

/* menu */

.navbar.navbar-inverse {
  width: 100%;
  margin-bottom: 0px !important;
  background: transparent;
}

.nav.navbar-nav {
  width: 100%;

  li {
    width: 25%;
    text-align: center;
    border: 1px solid #CCC !important;

    a {
      background-color: #000;
      color: #FFF;
      font-size: 1.4em;

      &:hover {
        background-color: #CCC !important;
        color: #000 !important;
      }
    }

    &.opt-menu-destacado a {
      color: #D4A26F !important;

      &:hover {
        background-color: #CCC !important;
        color: #000 !important;
      }
    }
  }
}

.dropdown-menu {
  width: 100%;
  padding: 0px;
  border: 2px solid #CCC !important;

  li {
    width: 100% !important;

    a {
      padding-top: 13px;
      padding-bottom: 13px;
    }
  }
}

.dropdown:hover .dropdown-menu {
  display: block;
  margin-top: 0;
}

.nav.navbar-nav li.order-menu a {
  background-color: #ED1624 !important;

  &:hover {
    background-color: #CD1624 !important;
    color: #FFF !important;
  }
}

.menu-mobil, .hamburger-menu {
  display: none;
}

.icono-menu-retiro {
  text-align: center;
  vertical-align: middle !important;
  padding-top: 10px;
  padding-left: 20px !important;
  padding-bottom: 8px;
}

.boton-orden-ingresado div {
  padding-left: 4px;
  padding-right: 4px;
}

.direccion-retiro-menu01 {
  font-size: 1em;
  color: #FFF !important;
  line-height: 0.9em;
  padding-top: 13px;
  text-align: left;
}

.direccion-retiro-menu02 {
  font-size: 0.7em;
  color: #979797;
}

.precio-menu-superior {
  color: #FED750;
  font-size: 1.6em;
  text-align: left;
  padding-top: 0.4em;
}

.contenedor-boton-orden-ingresado {
  background-color: #000 !important;
}

/* slider grande */

/* fotos promociones home */

.row {
  &.productos-promocion-home {
    padding-top: 35px;

    .col-lg-6 {
      margin-bottom: 60px;
    }
  }

  &.pie-pagina {
    background-color: #000;
    padding-top: 20px;
    padding-bottom: 10px;

    .col-lg-3 {
      padding-left: 50px;

      ul li {
        text-decoration: none;
        list-style: none;
      }
    }
  }
}

/* footer */

.footlist {
  padding: 0px;
}

.row.pie-pagina {
  .col-lg-3 {
    ul li a {
      color: #979797 !important;

      &:hover {
        color: #706F6F !important;
      }
    }

    p {
      color: #FFF;
    }
  }

  .pie-col-4 a {
    margin-right: 10px;
  }
}

/* CSS HOME / MENU RESPONSIVE */

@media only screen and (max-width: 768px) {
  .contenedor-menu-web {
    display: none;
  }

  .row.header-top .col-lg-6 {
    text-align: center;
  }

  .hamburger-menu {
    display: inline;
    text-align: center;
    margin: 0px;
  }

  .nav.navbar-nav {
    li {
      width: 100%;
      text-align: center;
      border: 1px solid #CCC !important;
    }

    margin: 0px;
  }

  .hamburger-menu .col-lg-12 {
    padding-bottom: 10px !important;
  }

  .row.productos-promocion-home {
    .col-lg-6 {
      margin-bottom: 20px;
    }

    padding-bottom: 20px;
  }

  .pie-col-4 {
    text-align: center;
    padding-bottom: 10px;
    padding-top: 0px !important;
  }

  .icono-menu-retiro {
    padding-left: 0px !important;
  }

  .precio-menu-superior, .direccion-retiro-menu01 {
    text-align: center;
    padding-top: 0px;
  }

  #bar {
    color: #FFF;
  }
}

@media only screen and (min-width: 769px) and (max-width: 1024px) {
  .row.header-top, .col-lg-6.ingreso-top {
    text-align: center;
  }
}

/* FIN HOME */

/* MODALS */

.modal-content {
  background-image: url(../img/bg.jpg);
  background-size: 100%;
  border-radius: 0px !important;
  text-align: center !important;
}

.modal-header {
  border: 0px;

  h3 {
    color: #D4A26F;
    font-size: 2em;
  }
}

.modal-dialog {
  margin: 12% auto !important;
}

.modal-body {
  h2 {
    color: #FFF !important;
    font-size: 1.5em;
  }

  color: #979797;

  #direccionreparto {
    padding: 3% 20%;
    border: 0px;
    border-radius: 4px;
    width: 100% !important;

    option {
      text-align: center !important;
      border: 0px !important;
    }
  }

  .cambiar-ubicacion {
    color: #FED750;

    &:hover {
      color: #979797;
    }
  }
}

.span_delivery {
  background-image: url(../img/delivery_pop_up.png);
  width: 200px;
  height: 100px;
  padding-right: 100px;
  background-position: right;
  background-repeat: no-repeat;
  padding-top: 40px;
}

.modal-delivery-left input[type=radio]:checked + label {
  background-image: url(../img/delivery_pop_up2.png);
  color: #FED750;
}

#delivery, #delivery2 {
  display: none;
}

.span_delivery2 {
  background-image: url(../img/pizza_hut_pop_up.png);
  width: 200px;
  height: 100px;
  padding-left: 100px;
  background-position: left;
  background-repeat: no-repeat;
  padding-top: 40px;
}

.modal-delivery-right input[type=radio]:checked + label {
  background-image: url(../img/pizza_hut_pop_up2.png);
  color: #FED750;
}

#button_modal_delivery {
  padding: 5px 40px;
  background-color: #ed1624;
  margin-top: 10px;
  border-radius: 4px;
  color: #FFF;
  border: 0px;
  font-weight: bold;
  font-size: 1.5em;
  width: 50%;
  margin-left: auto;
  margin-right: auto;

  &:hover {
    background-color: #fed750;
  }
}

.modal-ingresar-left {
  text-align: right;
  font-size: 1.4em;
  color: #FED750;
  padding-top: 0.3em;
}

.modal-ingresar-right {
  text-align: left;

  input {
    padding: 10px;
    width: 90%;
    border-radius: 4px;
    border: 0px;
    color: #706F6F;
  }
}

.textboxs-ingreso {
  padding-bottom: 15px;
}

.modal-producto-left {
  text-align: center;

  h3 {
    text-align: center;
  }
}

.modal-producto {
  text-align: left;
}

.row.ingredientes-modal-producto .col-lg-2 {
  padding: 0px 2px;
  font-size: 0.7em !important;
  text-align: center;

  img {
    margin-bottom: 5px;
  }
}

.ingrediente-first-modal {
  padding-left: 15px !important;
}

.modal-producto-left h3 {
  color: #FED750;
}

.form-control {
  height: auto !important;
}

.radio01 {
  visibility: hidden;
  position: absolute;
}

/* MODAL RESPONSIVE */

@media only screen and (max-width: 768px) {
  .span_delivery2 {
    padding-right: 100px;
    background-position: right;
    padding-left: 0px;
  }

  .modal-delivery-right {
    padding-top: 20px;
  }

  .modal-ingresar-left, .modal-ingresar-right {
    text-align: center;
  }
}

